import { mapState,mapGetters } from "vuex";

export default {
    data() {
        return {
            card_id: 0,
            coupon_id: 0,
            info: {},
        }
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            poster_img: state => state.mallConfig.__wxapp_img.poster,
        })
    },
    methods: {
        submitSave() {
            this.$showLoading({text: '生成中'});
            let that = this;
            let url;
            let para;
            if(that.card_id > 0) {
                url = that.$api.poster.card_share;
                para = {
                    cardId: that.card_id
                }
            }
            if(that.coupon_id > 0) {
                url = that.$api.poster.coupon_share;
                para = {
                    coupon_id: that.coupon_id
                }
            }
            that.$request({
                url: url,
                data: para
            }).then(info => {
                this.$hideLoading();
                let { code, data, msg } = info;
                if (code === 0) {
                    const pic_url = data.pic_url;

                    this.$utils.batchSave(pic_url, 'image').then(() => {
                        uni.showToast({title: '保存成功'});
                    });
                } else {
                    uni.showToast({title: msg, icon: 'none'});
                }
            }).catch(() => {
                this.$hideLoading();
            })
        },
        getList() {
            let that = this;
            let url;
            let para;
            if(that.card_id > 0) {
                url = that.$api.poster.card;
                para = {
                    cardId: that.card_id
                }
            }
            if(that.coupon_id > 0) {
                url = that.$api.poster.coupon;
                para = {
                    coupon_id: that.coupon_id
                }
            }
            that.$request({
                url: url,
                data: para
            }).then(response=>{
                if(response.code == 0) {
                    that.info = response.data;
                }else {
                    that.$hideLoading();
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if(options.card_id > 0) {
            that.card_id = options.card_id
        }
        if(options.coupon_id > 0) {
            that.coupon_id = options.coupon_id
        }
        that.getList();
    }
}
